@charset "utf-8";

//집콕+집밖

[class^="ico-"] {
    display: inline-block;
}

.ico-housein {
    @include rem(width, 60px);
    @include rem(height, 60px);
    background:url('/images/home/ico_house_in.png') center/contain;
}
.ico-houseout {
    @include rem(width, 60px);
    @include rem(height, 60px);
    background:url('/images/home/ico_house_out.png') center/contain;
}

.intro-home {
    @include rem(height, 254px);
    background:url('/images/home/img_banner_home.jpg') center/cover;
}

.title-area {
    text-align: center;
    @include rem(margin-bottom, 32px);

    .title-h2 {
        @include rem(font-size, 32px);
        font-weight: bold;
        color: #222;
    }
}

.main-home {
    @include rem(padding, 52px 0 80px);
    &::after {
        content:'';
        display: block;
        width:100%;
        @include rem(height, 15px);
        @include rem(margin-top, 80px);
        background-color: #f5f5f5;
    }

    
}

.main-home-swiper {
    .swiper-slide {
        a {
            display: block;
            cursor: pointer;
        }
    }

    .box-thumb {
        position: relative;
    }

    .thumb-img {
        display: block;
        width: 100%;
    }

    .thumb-category {
        position: absolute;
        @include rem(top, 10px);
        @include rem(right, 10px);
    }

    .thumb-btn {
        position: absolute;
        bottom: 0;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        @include rem(height, 48px);
        border: solid 1px #eee;
        background-color: #222;
        text-align: center;

        .txt {
            @include rem(font-size, 16px);
            font-weight: bold;
            color: #fff;
        }
    }

    .box-tag {
        display: flex;
        flex-wrap: wrap;
        @include rem(margin-top, 16px);
    }

    .tag{
        display:inline-flex;
        align-items: center;
        background-color:#f8f8f8;
        @include rem(height, 27px);
        @include rem(padding, 0 12px);
        @include rem(border-radius, 13.5px);
        @include rem(margin-right, 8px);

        &:last-child {
            margin: 0;
        }

        .txt{
            @include rem(font-size, 13px);
        }

        &.type01{
            background-color:#fff;
            border: solid 1px #222;
            .txt{color:#222;}
        }
    }

    .box-txt {
        @include rem(margin-top, 8px);

        .txt-tit {
            display: block;
            @include rem(font-size, 20px);
            color: #222;
            line-height: 1.5;
            @include rem(margin-bottom, 16px);
            @include rem(height, 59px);

            width: 100%;
            text-overflow: ellipsis;
            overflow: hidden;
            word-break: break-word;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical
        }

        .txt-desc {
            @include rem(font-size, 14px);
            color: #999;
            line-height: 1.42;

            width: 100%;
            text-overflow: ellipsis;
            overflow: hidden;
            word-break: break-word;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical
        }

        .txt-data {
            @include rem(font-size, 14px);
            color: #999;
            line-height: 1.42;
            
            display: flex;
            flex-wrap: wrap;

            .data-term {
                @include rem(width, 70px);
            }

            .data-desc {
                width: calc(100% - 7rem);
                @include rem(margin-bottom, 4px);

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}

.tab-buttons {
    .tab-wrap {
        display: flex;
        align-items: flex-end;
    }

    .tab-btn {
        display: block;
        flex: 1;
        @include rem(height, 64px);
        // background: #e8e7e8;
        border: 1px solid #ebebeb;
        border-bottom: 2px solid #444344;
        border-right: 0;
    
        &:last-child {
            border-right: 1px solid #ebebeb;
        }
        

        &.on {
            @include rem(height, 86);
            // background: #fff;
            border: 2px solid #444344;
            border-bottom: 0;
            box-shadow: 12px 0 12px -12px #9f9f9f;
            position: relative;
        }

        &:nth-child(1) {
            background:#f5f5f5 url('/images/home/img_title01_off.png') center no-repeat;
            @include rem(background-size, 206px 36px);
            &.on {
                background:#fff url('/images/home/img_title01_on.png') center no-repeat;
                @include rem(background-size, 306px 59px);
            }
        }
        &:nth-child(2) {
            background:#f5f5f5 url('/images/home/img_title02_off.png') center no-repeat;
            @include rem(background-size, 192px 39px);
            &.on {
                background:#fff url('/images/home/img_title02_on.png') center no-repeat;
                @include rem(background-size, 275px 56.5px);
            }
        }
        &:nth-child(3) {
            background:#f5f5f5 url('/images/home/img_title03_off.png') center no-repeat;
            @include rem(background-size, 209px 37px);
            &.on {
                background:#fff url('/images/home/img_title03_on.png') center no-repeat;
                @include rem(background-size, 303px 57.5px);
            }
        }
    }
}

.tab-contents {
    display: none;

    &.on {
        display: block;
    }
}


